<template>
  <div class="box">
    <h1 style="text-align:left;">User Selected: {{this.username}}</h1>
    <hr class="dropdown-divider">
    <span>
      <label class="switch">
        <input v-model="accountStatus" type="checkbox">
        <span class="slider round"></span>
      </label>
      <p v-if="accountStatus === true">Account is currently enabled</p>
      <p v-else>Account is now disabled</p>
    </span>
    <span>
      <button v-on:click="updateAccountStatus" class="button is-outlined is-link">Update</button>
      <button v-on:click="cancelAccountStatus" class="button">Cancel</button>
    </span>
  </div>
</template>

<script>
import Axios from 'axios'
import EventBus from '@/assets/js/EventBus.js'

export default {
  name: 'AdminAccountStatusPanel',
  created () {
    this.accountStatus = this.$store.getters.getAccountStatus
    this.username = this.$store.getters.getAdminSelectedUsername
  },
  data () {
    return {
      accountStatus: false,
      username: ''
    }
  },
  methods: {
    cancelAccountStatus: function () {
      EventBus.$emit('ToggleAdminAccountStatusView')
    },
    updateAccountStatus: function () {
      Axios({
        method: 'POST',
        url: this.$store.getters.getBaseAppUrl + this.$store.getters.getUpdateAccountStatusUri,
        headers: this.$store.getters.getRequestHeaders,
        data: {
          accountStatus: this.accountStatus,
          username: this.username
        }
      })
        .then((response) => {
          this.$store.dispatch('updateErrorMessage', 'Success')
          EventBus.$emit('error')
          this.cancelAccountStatus()
        })
        .catch((error) => {
          this.$store.dispatch('updateErrorMessage', error)
          EventBus.$emit('error')
          this.cancelAccountStatus()
        })
    }
  }
}
</script>

<style>
/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {display:none;}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
</style>



